h1, h2, h3, h4, h5, h6 {
  margin: 0;
}

.App {
  max-width: 480px;
  margin: 0 auto;
}

.App-header {
  background-color: #76c6e7;
  color: white;
  padding: 1% 2%;
}

.App-brand {
  color: black;
  font-size: calc(1em + 1vmin);
  margin-bottom: 1vh;
}

.App-logo {
}

.App-logo img {
  height: calc(4em + 10vmin);
}

.App-loading {
  min-height: 30vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-error {
  min-height: 20vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: red;
  font-size: 1.2em;
}

.App-link {
  color: #006fb7;
  font-weight: bold;
}

.App-content {
  padding: 1% 2%;
  font-size: 1.2em;
}

.Journal-header {

}

.Journal-title {
  font-style: italic;
}


.Articles {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
}

.Articles article {
  margin: 0.5em 0;
  border: 1px solid grey;
  padding: 0.5em
}

.Articles-none{
  margin-top: 1em;
  border-top: 1px solid grey;
  padding-top: 1em;
}

.Article-author {
  font-size: 1.1em;
  margin-bottom: 0.3em;
}

@media screen and (min-width:800px) {
  .App {
    max-width: 980px;
  }

  .Articles {
      flex-direction: row;
      flex-wrap: wrap;
  }

  .Articles article {
    width: 47%;
  }
}
